'use strict';

jQuery(function ($) {
	var win;
	var productInfo;
	var header;
	var lastWidth;
	var currentWidth;
	var winW, winH;
	var lastScrollTop;
	var scrollTop;
	var productInfoHeight;
	var productAddToCartBar;

	function init() {

		currentWidth = lastWidth = $(window).width();

		if ($('.ProductRelated').length > 0) {
			productRelatedSlideshow();
		}
	}

	function productRelatedSlideshow() {

		$('.ProductRelated .ProductGrid').each(function () {
			$(this).on('init', function (event, slick) {});
		});

		$('.ProductRelated .ProductGrid').slick({
			slidesToShow: 4,
			slidesToScroll: 4,
			lazyLoad: 'ondemand',
			arrows: false,
			autoplay: false,
			autoplaySpeed: 4000,
			adaptiveHeight: false,
			draggable: false,
			infinite: true,
			dots: false,
			fade: false,
			responsive: [{
				breakpoint: 768,
				settings: {
					slidesToShow: 2,
					slidesToScroll: 2,
					infinite: true,
					dots: true
				}
			}]
		});
	}

	function isTouchDevice() {
		return 'ontouchstart' in document.documentElement;
	}

	init();
});