'use strict';

jQuery(function ($) {

	function init() {

		if ($('.BlogHeroCarousel').length > 0) {
			blogSlideshow();
			$(window).on('resize', resizeUI);
			resizeUI();
		}
	}

	function blogSlideshow() {
		$('.BlogHeroCarousel .slideshow').each(function () {
			$(this).on('init', function (event, slick) {
				var p = slick.$slider.parent();
				p.find('.thumbnails .thumb').removeClass('active');
				p.find('.thumbnails .thumb').eq(0).addClass('active');
				$(window).trigger('resize');
			});
		});

		var isDots = $('.BlogHeroCarousel .slideshow .BlogHeroSlide').length > 1;

		$('.BlogHeroCarousel .slideshow').slick({
			slidesToShow: 1,
			slidesToScroll: 1,
			arrows: true,
			autoplay: true,
			autoplaySpeed: 4000,
			adaptiveHeight: false,
			draggable: false,
			fade: true,
			dots: isDots
		}).on('beforeChange', function (event, slick, currentSlide, nextSlide) {
			var p = slick.$slider.parent();
			p.find('.thumbnails .thumb').removeClass('active');
			p.find('.thumbnails .thumb').eq(nextSlide).addClass('active');
		});
	}

	function resizeUI() {
		$('.BlogHeroCarousel .slideshow .BlogHeroSlide .text-wrap').height('auto');
		var h = 0;
		var oh = 0;

		$('.BlogHeroCarousel .slideshow .BlogHeroSlide .text-wrap').each(function () {
			oh = $(this).height();
			if (oh > h) {
				h = oh;
			}
		});

		$('.BlogHeroCarousel .slideshow .BlogHeroSlide .text-wrap').height(h);
	}

	function isTouchDevice() {
		return 'ontouchstart' in document.documentElement;
	}

	init();
});