'use strict';

jQuery(function ($) {

  var isMenuOpen = false;
  var isSubMenuOpen = false;
  var isSearchOpen = false;
  var isAccountOverlayOpen = false;
  var isCartOverlayOpen = false;
  var menuTimeout;
  var lastWidth;
  var currentWidth;
  var mouseTop = $('body').hasClass('announcement-bar-active') ? 604 : 600;

  function init() {
    $(window).on('resize', resizeAdjustments);
    $(window).on('dismissPanels', dismissPanels);
    initClicks();
    resizeAdjustments();
  }

  function initClicks() {
    $('#SiteHeader .hamburger').click(function (e) {
      e.preventDefault();
      if (!isMenuOpen) {
        openMenu();
      } else {
        closeMenu();
      }
      return false;
    });

    $('#SiteHeader .search-btn').click(function (e) {
      e.preventDefault();
      if (!isSearchOpen) {
        openSearch();
      } else {
        closeSearch();
      }
      return false;
    });

    $('#SiteHeader .search-close').click(function (e) {
      e.preventDefault();
      closeSearch();
      return false;
    });

    $('.taphover, .button').on('touchstart', function (e) {
      'use strict'; //satisfy code inspectors

      var link = $(this); //preselect the link
      if (link.hasClass('hover')) {
        return true;
      } else {
        link.addClass('hover');
        $('a.taphover').not(this).removeClass('hover');
        $('a.button').not(this).removeClass('hover');
        //e.preventDefault();
        return true; //extra, and to make sure the function has consistent return points
      }
    });

    $('.taphover, .button').on('touchend', function (e) {
      'use strict'; //satisfy code inspectors

      var link = $(this); //preselect the link
      if (link.hasClass('hover')) {
        link.removeClass('hover');
        return true;
      }
    });

    $('.main-menu > li.menu-item-has-children > a').mouseover(function () {
      if (currentWidth >= 1024) {
        var id = $(this).attr('rel');
        showSubMenu(id);
      }
    });

    $('.main-menu > li:not(.menu-item-has-children) > a').mouseover(function () {
      if (currentWidth >= 1024) {
        hideSubMenu();
      }
    });

    $('body').mousemove(function (e) {
      if (currentWidth >= 1024) {
        if (e.clientY > mouseTop && isSubMenuOpen) {
          hideSubMenu();
        }
      }
    });

    $('.sub-menu').mouseleave(function () {
      if (currentWidth >= 1024) {
        hideSubMenu();
      }
    });

    $('.main-menu.mobile > li.menu-item-has-children > a').on('mousedown', function (e) {
      // if (currentWidth < 1024) {
      //   e.preventDefault();
      //   if(!$(this).closest('li').hasClass('active')){
      //     var id = $(this).attr('rel');
      //     showSubMenu(id);
      //     return false;
      //   } else {
      //     hideSubMenu();
      //   }
      // }
    });

    $('.main-menu.mobile > li.menu-item-has-children > a').on('mouseup', function (e) {
      if (currentWidth < 1024) {
        //e.preventDefault();
        return false;
      }
    });

    $('.main-menu.mobile > li.menu-item-has-children > a').click(function (e) {
      if (currentWidth < 1024) {
        e.preventDefault();
        if (!$(this).closest('li').hasClass('active')) {
          var id = $(this).attr('rel');
          showSubMenu(id);
          return false;
        } else {
          hideSubMenu();
        }
      }
    });

    $('#SiteHeader .login').click(function (e) {
      e.preventDefault();
      if (isAccountOverlayOpen) {
        closeAccountOverlay();
      } else {
        openAccountOverlay();
      }
      return false;
    });

    $('#SiteHeader .cart-btn').click(function (e) {
      e.preventDefault();
      isCartOverlayOpen = $('body').hasClass('cart-overlay-active');
      if (isCartOverlayOpen) {
        closeCartOverlay();
      } else {
        openCartOverlay();
      }
      return false;f;
    });
  }

  function selectFirst() {
    var id = 'shop';
    showSubMenu(id);
  }

  function toggleMenu() {
    if (this.state.menu === 'closed') {
      this.openMenu();
    } else {
      this.closeMenu();
    }
  }

  function showSubMenu(id) {
    hideSubMenu();
    isSubMenuOpen = true;
    // desktop
    $('#sub-menu').addClass('active');
    $('#sub-menu .' + id).addClass('active');

    // mobile
    $('#main-menu .main-menu.mobile li a.' + id).closest('li').addClass('active');
    var submenu = $('#main-menu .main-menu.mobile li.active .sub-menu');
    var h = submenu.attr('data-max-height');
    submenu.css('height', h + 'px');
  }

  function hideSubMenu() {
    isSubMenuOpen = false;
    $('#sub-menu').removeClass('active');
    $('#sub-menu .sub-menu').removeClass('active');
    $('#main-menu .main-menu.mobile li').removeClass('active');
    $('#main-menu .main-menu.mobile li .sub-menu').attr('style', null);
  }

  function openMenu() {

    selectFirst();

    isMenuOpen = true;

    closeCartOverlay();
    closeAccountOverlay();
    closeSearch();

    document.documentElement.style.overflow = 'hidden'; // firefox, chrome
    document.documentElement.style.height = '100%';
    document.body.scroll = "no"; // ie only
    document.body.style.overflow = 'hidden';
    document.body.style.height = '100%';

    if (menuTimeout) {
      //clearTimout(menuTimeout);
      menuTimeout = null;
    }

    $('#SiteHeader #main-menu').css('transition-duration', '.4s');
    $('body').addClass('menu-open');
  }

  function closeMenu() {
    isMenuOpen = false;
    document.documentElement.style.overflow = ''; // firefox, chrome
    document.documentElement.style.height = '';
    document.body.scroll = "yes"; // ie only
    document.body.style.overflow = '';
    document.body.style.height = '';

    $('body').removeClass('menu-open');

    menuTimeout = setTimeout(function () {
      $('#SiteHeader #main-menu').css('transition-duration', '0s');
    }, 500);

    hideSubMenu();
  }

  function openSearch() {
    closeAccountOverlay();
    closeCartOverlay();
    hideSubMenu();
    isSearchOpen = true;
    $('#SiteHeader #desktop-search input').val('');
    $('#SiteHeader #desktop-search input').focus();
    $('body').addClass('search-open');
  }

  function closeSearch() {
    isSearchOpen = false;
    document.documentElement.style.overflow = ''; // firefox, chrome
    document.documentElement.style.height = '';
    document.body.scroll = "yes"; // ie only
    document.body.style.overflow = '';
    document.body.style.height = '';
    $('body').removeClass('search-open');
  }

  function openAccountOverlay() {
    isAccountOverlayOpen = true;
    closeMenu();
    closeSearch();
    closeCartOverlay();
    hideSubMenu();
    $('body').addClass('account-overlay-open');
  }

  function closeAccountOverlay() {
    isAccountOverlayOpen = false;
    $('body').removeClass('account-overlay-open');
  }

  function openCartOverlay() {
    isCartOverlayOpen = true;
    closeMenu();
    closeSearch();
    closeAccountOverlay();
    hideSubMenu();
    $(window).trigger('showCart');
  }

  function closeCartOverlay() {
    isCartOverlayOpen = false;
    $(window).trigger('hideCart');
  }

  function isTouchDevice() {
    return 'ontouchstart' in document.documentElement;
  }

  function resizeAdjustments() {
    lastWidth = currentWidth;
    currentWidth = $(window).width();

    setSubmenuHeights();

    if (isMenuOpen) {
      if (currentWidth >= 1024 && lastWidth < 1024) {
        if (isMenuOpen) {
          closeMenu();
        }
      }
    }
  }

  function setSubmenuHeights() {
    $('.main-menu.mobile .sub-menu').each(function () {
      var items = $(this).find('> li');
      var len = items.length;
      var itemH = items.first().height();
      var h = len * itemH;
      $(this).attr('data-max-height', h);
    });
  }

  function dismissPanels() {
    closeSearch();
    closeAccountOverlay();
  }

  init();
});