'use strict';

jQuery(function ($) {

	var isAnimating;
	var native_width = 0;
	var native_height = 0;
	var magnify_offset;
	var mx = 0;
	var my = 0;
	var lastXPos = 0;
	var lastYPos = 0;
	var lastScrolledXPos = 0;
	var lastScrolledYPos = 0;
	var paginationHeight = 20;
	var currentProduct;
	var win;
	var productInfo;
	var header;

	var lastWidth;
	var currentWidth;
	var winW, winH;

	var lastScrollTop;
	var scrollTop;
	var productInfoHeight;
	var productAddToCartBar;

	function init() {

		currentWidth = lastWidth = $(window).width();

		if ($('.ProductInfo').length > 0) {
			productInfo = $('.ProductInfo');
			header = $('#SiteHeader');
			productAddToCartBar = $('.ProductAddToCartBar');
			win = $(window);

			productSlideshow();
			productAccordion();

			var productID = productInfo.attr('data-product-id');
			currentProduct = LOCAL_PRODUCTS[productID];

			$('.ProductInfo .details .icon-minus').click(function (event) {
				event.stopPropagation();
				var cartItem = $(this).closest('.details');
				var qty = Number(cartItem.find('.qty').text()) - 1;
				if (qty > 0) {
					$('.ProductInfo .details .qty').text(qty);
					$('.ProductInfo #Quantity').attr('value', qty);
					$(window).trigger('product-quantity-update');
				}
				return false;
			});

			$('.ProductInfo .details .icon-plus').click(function (event) {
				event.stopPropagation();
				var cartItem = $(this).closest('.details');
				var qty = Number(cartItem.find('.qty').text()) + 1;
				if (qty <= 100) {
					$('.ProductInfo .details .qty').text(qty);
					$('.ProductInfo #Quantity').attr('value', qty);
					$(window).trigger('product-quantity-update');
				}

				return false;
			});

			$('.ProductInfo .add-to-cart').click(function (event) {
				event.stopPropagation();
				var productID = $(this).closest('form').attr('data-product-id');
				var variantID = $('#variantID').val();
				var quantity = Number($('#Quantity').val());
				win.trigger('addSingleItemToCart', [productID, variantID, quantity]);
				return false;
			});

			$('.ProductInfo .details .specs').click(function (event) {
				event.stopPropagation();
				showSpecsOverlay();
				return false;
			});

			$('.ProductSpecs.modalwrap .close').click(function (event) {
				event.stopPropagation();
				hideSpecsOverlay();
				return false;
			});

			$('.ProductSpecs.modalwrap').click(function (event) {
				if ($(event.target).hasClass('modalwrap')) {
					hideSpecsOverlay();
				}
			});

			$('.ProductInfo .reviews-anchor').click(function (event) {
				event.stopPropagation();
				animateToID('shopify-section-product-reviews');
				return false;
			});

			win.on('scroll', windowScrolled);
			win.on('resize', windowResized);
			win.trigger('resize');
		}
	}

	function productSlideshow() {

		$('.ProductInfo .slideshow').each(function () {
			$(this).on('init', function (event, slick) {
				var p = slick.$slider.parent();
				p.find('.thumbnails .thumb').removeClass('active');
				p.find('.thumbnails .thumb').eq(0).addClass('active');
				$(window).trigger('resize');
				windowResized();
			});
		});

		$('.ProductInfo .slideshow').slick({
			slidesToShow: 1,
			slidesToScroll: 1,
			lazyLoad: 'ondemand',
			arrows: true,
			autoplay: false,
			autoplaySpeed: 4000,
			adaptiveHeight: true,
			draggable: false,
			fade: true
		}).on('beforeChange', function (event, slick, currentSlide, nextSlide) {
			var p = slick.$slider.parent();
			p.find('.thumbnails .thumb').removeClass('active');
			p.find('.thumbnails .thumb').eq(nextSlide).addClass('active');
		});

		$('.ProductInfo .thumbnails .thumb').click(function (e) {
			e.preventDefault();
			var index = $(this).index();
			var p = $(this).closest('.slideshow-wrap');
			p.find('.slideshow').slick('slickGoTo', index);
			return;
		});

		$('.ProductInfo .slide').first().find('img').on('load', function () {
			windowResized();
		});

		$('.ProductInfo .slideshow').click(function (event) {
			event.stopPropagation();
			$(this).slick('slickNext');
			return false;
		});
	}

	function productAccordion() {
		var allPanels = $('.ProductInfo .accordion > dd').hide();
		$('.ProductInfo .accordion > dd').first().show();
		$('.ProductInfo .accordion > dt').first().addClass('active');
		$('.ProductInfo .accordion > dt > a').click(function () {
			updateAccordion($(this).parent());
			return false;
		});
	}

	function updateAccordion(el) {
		if (el.hasClass('active')) {
			el.next().stop().slideUp();
			el.removeClass('active');
		} else {
			el.next().stop().slideDown();
			el.addClass('active');
		}
	}

	function windowResized(event) {
		productInfoHeight = productInfo.height();
		windowScrolled();
	}

	function windowScrolled(event) {
		lastScrollTop = scrollTop;
		scrollTop = win.scrollTop();

		/*if(lastScrollTop > scrollTop){
  	if(header.hasClass('scroll-hide')){
  		header.removeClass('scroll-hide');
  		productAddToCartBar.removeClass('scroll-show');
  	}
  } else if(scrollTop > productInfoHeight){
  	if(!header.hasClass('scroll-hide')){
  		header.addClass('scroll-hide')
  		productAddToCartBar.addClass('scroll-show');
  	}
  }*/
	}

	function showSpecsOverlay() {
		resizeUI();
		//if(winW >= 768){
		$('body').css('overflow', 'hidden');
		//}

		$('.ProductSpecs.modalwrap').addClass('active');
		$('.ProductSpecs.modalwrap').show();
		$(window).trigger('resize');
		$('.ProductSpecs.modalwrap').hide();
		$('.ProductSpecs.modalwrap .specs-wrap').scrollTop(0);
		$('.ProductSpecs.modalwrap').fadeIn({ duration: 500, start: resizeUI, step: resizeUI, complete: resizeUI });
	}

	function hideSpecsOverlay() {
		$('body').css('overflow', 'scroll');
		$('.ProductSpecs.modalwrap').removeClass('active');
		$('.ProductSpecs.modalwrap').fadeOut();
		$(window).trigger('resize');
	}

	function resizeUI() {
		var headerH = $('#SiteHeader').height();
		var viewportRatio = (document.body.clientHeight - headerH) / document.body.clientWidth;
		lastWidth = currentWidth;
		currentWidth = $(window).width();
		winW = window.innerWidth;
		winH = window.innerHeight;
		checkModals();
	}

	function isTouchDevice() {
		return 'ontouchstart' in document.documentElement;
	}

	function checkModals() {
		// Modal
		$('.modalwrap .modal').each(function () {
			var t = winH - $(this).outerHeight(true);
			t = t > 0 ? t / 2.0 : 0;

			if (winW >= 768) {
				$(this).css('top', t + 'px');
				$(this).height('auto');
				setHeightByChildren($(this));
				var h = $(this).height();
				if (winH > h) {
					$(this).addClass('v-center');
				} else {
					$(this).removeClass('v-center');
				}
			} else {
				$(this).css('top', '0px');
				$(this).height('auto');
				$(this).removeClass('v-center');
			}
		});
	}

	function setHeightByChildren(parent) {
		var h = 0;
		parent.children().each(function () {
			if ($(this).is(":visible")) {
				h += $(this).outerHeight(true);
			}
		});
		parent.height(h);
	}

	function animateToID(id) {
		isAnimating = true;
		var targetTop = getTopFromID(id);
		$('html, body').stop().animate({ scrollTop: targetTop }, 600, 'easeOutCubic', function () {
			isAnimating = false;
		});
	}

	function getTopFromID(id) {
		var hh = $('#SiteHeader').height() + 1;
		var h2 = $('#shopify-section-header .announcement-bar').height();
		var vw = $(window).width() * .09;
		var t = $('#' + id).offset().top;
		return t - hh - h2 + vw;
	}

	init();
});