'use strict';

jQuery(function ($) {
  function init() {
    $('#zendesk-button').click(function (event) {
      event.stopPropagation();
      go();
      return false;
    });

    window.addEventListener('message', function (event) {
      //console.log('message received', event );
      // IMPORTANT: Check the origin of the data! 
      if (event && event.data && event.data.indexOf) {

        if (event.data.indexOf('showChat') > -1) {
          go();
        } else {
          return;
        }
      } else {
        return;
      }
    });
  }

  function go() {

    window.zESettings = {
      webWidget: {
        chat: {
          suppress: false
        },
        contactForm: {
          suppress: false
        },
        helpCenter: {
          suppress: false
        },
        talk: {
          suppress: false
        },
        answerBot: {
          suppress: false
        }
      }
    };

    zE.activate();

    $('#zendesk-button').addClass('hidden');

    zE('webWidget:on', 'close', function () {
      $('#zendesk-button').removeClass('hidden');
    });
  }

  init();
});