'use strict';

jQuery(function ($) {

  var $body = $('body');
  var isAnimating;

  function init() {
    initClasses();
    initHash();
  }

  function initHash() {
    if ($body.attr('id') === 'collections' || $body.hasClass('template-search')) {
      // Adjust scroll for header if there's a URL hash
      setTimeout(function () {
        if (window.location.hash) {
          var hh = $('#SiteHeader').height() + 1;
          var t = $('.ProductCollection' + window.location.hash).offset().top;
          var id = window.location.hash.replace('#', '');
          var targetTop = getTopFromID(id);
          $(window).scrollTop(targetTop);
          setTimeout(function () {
            $('.HeroBasic').css('visibility', 'visible');
          }, 1000);
        }
      }, 200);

      $('.sub-menu.shop li a').click(function (e) {
        e.preventDefault();
        var hash = $(this).attr('data-id');
        animateToID(hash);
        return false;
      });

      $(window).on('hashchange', function (e) {
        e.preventDefault();
        animateToTarget(window.location.hash);
        return false;
      });
    }
  }

  function animateToID(id) {
    isAnimating = true;
    var targetTop = getTopFromID(id);
    $('html, body').stop().animate({ scrollTop: targetTop }, 600, 'easeOutCubic', function () {
      isAnimating = false;
    });
  }

  function getTopFromID(id) {
    var hh = $('#SiteHeader').height() + 1;
    var h2 = $('#shopify-section-header .announcement-bar').height();
    var t = $('.ProductCollection#' + id).offset().top;
    return t - hh - h2;
  }

  function initClasses() {
    $('.ProductGrid').each(function () {
      var len = $(this).find('.ProductGridItem').length;

      if (len < 4) {
        $(this).addClass('only' + len);
      }
    });
  }

  init();
});