'use strict';

jQuery(function ($) {

	var currentSection;
	var interval;
	var cnt = -1;
	var maxCnt = 4;
	var arr = [100, 0, 0, 0, 0];
	var intervalDuration = 500;

	function init() {
		$('.image-with-text').mouseenter(function () {
			stopAni();
			currentSection = $(this);
			startAni();
		});

		$('.image-with-text').mouseleave(function () {
			stopAni();
		});
	}

	function startAni() {
		tick();
		var frameCnt = Number(currentSection.find('.image__image').attr('data-animation-frame-count'));
		arr = [];

		for (var i = 0; i < frameCnt; i++) {
			var dist = (i + 1) * 100;
			arr.push(dist);
		}

		arr.push(0);
		arr.push(0);
		arr.push(0);
		arr.push(0);

		maxCnt = arr.length - 1;
		interval = setInterval(tick, intervalDuration);
	}

	function stopAni() {
		//console.log('stopAni');
		cnt = -1;
		if (currentSection) {
			currentSection.find('.image__image .image').css('background-position', '0 0');
		}
		currentSection = null;
		clearInterval(interval);
		interval = null;
	}

	function tick() {
		cnt++;
		if (cnt == maxCnt - 1) {
			cnt = 0;
			clearInterval(interval);
		} else {
			currentSection.find('.image__image .image').css('background-position', '0 ' + arr[cnt] + '%');
		}
		//console.log('cnt', cnt, -arr[cnt]);
	}

	init();
});