'use strict';

jQuery(function ($) {
	var win;
	function init() {

		if ($('.template-product .ScientificAdvisors .advisor-list').length > 0) {
			productScientificAdvisorsSlideshow();
		}
	}

	function productScientificAdvisorsSlideshow() {

		$('.template-product .ScientificAdvisors .advisor-list').slick({
			slidesToShow: 3,
			slidesToScroll: 1,
			lazyLoad: 'ondemand',
			arrows: true,
			autoplay: false,
			autoplaySpeed: 4000,
			adaptiveHeight: false,
			draggable: false,
			infinite: true,
			dots: false,
			fade: false,
			responsive: [{
				breakpoint: 1024,
				settings: {
					slidesToShow: 2,
					slidesToScroll: 1,
					infinite: true
				}
			}, {
				breakpoint: 768,
				settings: {
					slidesToShow: 1,
					slidesToScroll: 1,
					infinite: true
				}
			}]
		});
	}

	function isTouchDevice() {
		return 'ontouchstart' in document.documentElement;
	}

	init();
});