'use strict';

jQuery(function ($) {

	var isAnimating;
	var native_width = 0;
	var native_height = 0;
	var magnify_offset;
	var mx = 0;
	var my = 0;
	var lastXPos = 0;
	var lastYPos = 0;
	var lastScrolledXPos = 0;
	var lastScrolledYPos = 0;
	var paginationHeight = 20;
	var currentProduct;
	var win;
	var productInfo;
	var header;

	function init() {
		var win = $(window);

		if ($('.ProductTestimonials').length > 0) {
			productTestimonialsSlideshow();
		}
	}

	function productTestimonialsSlideshow() {

		$('.ProductTestimonials .Testimonials').each(function () {
			$(this).on('init', function (event, slick) {});
		});

		$('.ProductTestimonials .Testimonials').slick({
			slidesToShow: 1,
			slidesToScroll: 1,
			lazyLoad: 'ondemand',
			arrows: false,
			autoplay: false,
			autoplaySpeed: 4000,
			adaptiveHeight: false,
			draggable: false,
			infinite: true,
			swipe: isTouchDevice(),
			dots: true,
			fade: false
		}).on('beforeChange', function (event, slick, currentSlide, nextSlide) {});

		$('.ProductTestimonials').click(function (event) {
			event.stopPropagation();
			$('.ProductTestimonials .Testimonials').slick('slickNext');
			return false;
		});
	}

	function isTouchDevice() {
		return 'ontouchstart' in document.documentElement;
	}

	init();
});