  function getCart(ignoreNotify) {
    $.ajax({
      type: 'GET',
      url: '/cart',
      xhrFields: {
        withCredentials: true
      },
      dataType: 'json',
      headers: {
        'Access-Control-Allow-Origin': '*',
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      },
      success: function success(cartdata) {
        //console.log('cartdata', cartdata);
        if (cartdata.items.length > 0) {
          updateCartDisplay(cartdata, ignoreNotify);
        } else {
          updateCartDisplayEmpty(cartdata, ignoreNotify);
        }

        if (!ignoreNotify) {
          // Show the cart contents
          if (!$body.hasClass('cart-overlay-active')) {
            $('.cart-panel').addClass('slide-left');
            toggleCart();
          } else {
            $('.cart-panel').addClass('slide-left');
          }
          $('.CartOverlay .cart-panel section').scrollTop(0);
        }
      },
      error: function error(cartdata) {
        console.log('error');
      }
    });
  }

  function addItemToCart(variant_id, quantity, shipping_interval_frequency, shipping_interval_unit_type, subscription_id, ignoreNotify) {

    var data;

    if (shipping_interval_frequency === "0") {
      data = {
        "quantity": quantity,
        "id": variant_id
      };
    } else {
      data = {
        "quantity": quantity,
        "id": variant_id
      };
    }

    $.ajax({
      type: 'POST',
      url: '/cart/add.js',
      data: data,
      dataType: 'json',
      success: function success(cartdata) {

        //console.log('CART_DATA', cartdata);
        if (cartdata) {
          updateDataLayer(cartdata);
          getCart(ignoreNotify);
        } else {
          updateCartDisplayEmpty();
        }
      },
      error: function error(err) {
        //alert(err.responseJSON.description);
        console.log('ERROR', err);
      }
    });
  }

  function updateDataLayer(cartdata) {
    //console.log('cartdata', cartdata);
    dataLayer.push({ 'event': 'generic_event',
      'eventCategory': 'AW_ATC',
      'AW_ProductID': cartdata.variant_id,
      'AW_ProductName': cartdata.title,
      'AW_ProductGroupID': cartdata.product_id,
      'AW_ProductPrice': cartdata.price / 100
    });
  }

  function getLocalProductVariant(variant_id) {
    if (!variant_id) {
      return;
    }

    var localProductVariant;

    for (var product in LOCAL_PRODUCTS) {
      if (product.variants) {
        var len = product.variants.length;
        var v;
        for (var i = 0; i < len; i++) {
          v = product.variants[i];
          if (Number(v.variantId) === Number(variant_id)) {
            localProductVariant = v;
            break;
          }
        }
      }
    }

    return localProductVariant;
  }

  function addSingleItemToCart(productID, variantID, quantity) {
    addItemToCart(variantID, quantity || 1);
  }

