'use strict';

jQuery(function ($) {
	var win;
	function init() {

		if ($('.SocialPosts .post-list').length > 0) {
			slideshow();
		}
	}

	function slideshow() {

		$('.SocialPosts .post-list').slick({
			slidesToShow: 1,
			slidesToScroll: 1,
			lazyLoad: 'ondemand',
			arrows: false,
			autoplay: false,
			autoplaySpeed: 4000,
			adaptiveHeight: false,
			draggable: false,
			infinite: true,
			dots: true,
			fade: false,
			responsive: [{
				breakpoint: 1024,
				settings: {
					slidesToShow: 1,
					slidesToScroll: 1,
					infinite: true
				}
			}, {
				breakpoint: 768,
				settings: {
					slidesToShow: 1,
					slidesToScroll: 1,
					infinite: true
				}
			}]
		});
	}

	function isTouchDevice() {
		return 'ontouchstart' in document.documentElement;
	}

	init();
});