'use strict';

jQuery(function ($) {

	var winH = window.innerHeight;
	var win = $(window);
	var scrollY,
	    lastScrollY = 0;
	var scrollDirection = 0;
	var oldIndex = 1;
	var newIndex = 1;
	var targetStep;
	var lastTargetStep;
	var el;
	var slideCount;

	var slideshow;
	function init() {

		if ($('.ScientificMetrics .metrics-list').length > 0) {
			el = $('.ScientificMetrics').first();
			productScientificMetricsSlideshow();
			resizeAdjustments();
			$(window).on('resize', resizeAdjustments);
			$(window).on('scroll', checkScroll);
		}
	}

	function productScientificMetricsSlideshow() {

		slideCount = $('.ScientificMetrics .metrics-list .ScienceMetric').length;

		slideshow = $('.ScientificMetrics .metrics-list').slick({
			slidesToShow: 1,
			slidesToScroll: 1,
			lazyLoad: 'ondemand',
			arrows: false,
			autoplay: false,
			autoplaySpeed: 4000,
			adaptiveHeight: false,
			draggable: false,
			infinite: false,
			dots: false,
			fade: true,
			swipe: false
		}).on('beforeChange', function (event, slick, currentSlide, nextSlide) {
			var li = $('.ScientificMetrics .metrics-nav li');
			li.removeClass('active');
			$('.ScientificMetrics .metrics-nav').find('li').eq(nextSlide).addClass('active');
		});

		$('.ScientificMetrics .metrics-nav li button').click(function (e) {
			e.preventDefault();
			var index = Number($(this).attr('rel'));
			slideshow.slick('slickGoTo', index);
			setPageScroll(index);
			return false;
		});

		$('.ScientificMetrics .ScienceMetric .img-wrap').click(function (e) {
			e.preventDefault();
			slideshow.slick('slickNext');
			setPageScroll(slideshow.slick('slickCurrentSlide'));
			return false;
		});

		$('.ScientificMetrics .metrics-nav li').first().addClass('active');
	}

	function isTouchDevice() {
		return 'ontouchstart' in document.documentElement;
	}

	function resizeAdjustments() {
		// fit everything to the screen
		winH = window.innerHeight;
		checkScroll();
	}

	function checkScroll() {
		targetStep = Math.max(Math.round((getYOffset() - el.offset().top) / winH), 0); // what frame to animate to

		if (targetStep > slideCount - 1) {
			targetStep = slideCount - 1;
		} else if (targetStep < 0) {
			targetStep = 0;
		}

		if (targetStep != lastTargetStep) {
			lastTargetStep = targetStep;
			slideshow.slick('slickGoTo', targetStep);
		}
	}

	function setPageScroll(index) {
		$(window).scrollTop(el.offset().top + index * winH);
	}

	function getYOffset() {
		// get distance scrolled from the top
		var pageY;
		if (typeof window.pageYOffset == 'number') {
			pageY = window.pageYOffset;
		} else {
			pageY = document.documentElement.scrollTop; // IE
		}
		return pageY;
	}

	function pad(number, length) {
		// pad numbers with leading zeros for JPEG sequence file names
		var str = '' + number;while (str.length < length) {
			str = '0' + str;
		}return str;
	}

	init();
});