'use strict';

jQuery(function ($) {

	var isPlaying;
	var wasPlaying;
	var player;
	var iframe;
	var st;
	var win, winW, winH;
	var header = $('#header');
	var headerH = header.height();
	var $heroVideo = $('#hero-video');
	var $heroImage = $('#hero-image');
	var $titleHolder = $('#hero-image .title-holder');
	var $endHolder = $('#hero-image .end-holder');
	var $loader = $('.HeroVideo .spinner');
	var isAutoPlay = false;
	var isLoaderHidden;
	var isPosterHidden = false;

	function init() {

		var videoID = $heroVideo.attr('data-video-id');
		iframe = document.getElementById('hero-video');
		win = $(window);
		win.on('resize', windowResized);
		win.on('show-video-modal', showVideoModal);
		win.on('hide-video-modal', hideVideoModal);

		initClicks();
	}

	function initClicks() {
		$('.Modal.video .close').click(function (e) {
			e.preventDefault();
			$(window).trigger('hide-video-modal');
			return false;
		});

		$('.button.modal-video').click(function (e) {
			e.preventDefault();
			//$(window).trigger('show-video-modal');
			showVideoModal();
			return false;
		});
	}

	function showVideoModal() {
		$('body').addClass('modal-video-active');
		$('.Modal.video').stop().hide().fadeIn();
		autoPlayVideo();
	}

	function hideVideoModal() {
		$('.Modal.video').stop().fadeOut(function () {
			$('body').removeClass('modal-video-active');
		});
		pauseVideo();
	}

	function autoPlayVideo() {
		if (!player) {
			var autoplaySrc = $heroVideo.attr('data-autoplay-src');
			$heroVideo.attr('src', autoplaySrc);
			player = new Vimeo.Player(iframe);

			if (player) {
				player.on('ended', function () {
					player.setCurrentTime(0);
					hideVideoModal();
				});

				player.on('play', function () {
					isPlaying = true;
				});

				player.on('timeupdate', function (event) {
					$heroVideo.css('opacity', '1');
					if (event.percent > .98 && isPlaying) {
						//player.pause();
						//hideVideoModal();
					}
				});

				player.setLoop(false).then(function (loop) {
					// loop was turned off
				}).catch(function (error) {
					// an error occurred
				});

				player.play();
			}
		} else {
			player.setCurrentTime(0);
			player.play();
		}
		$('.HeroVideo').addClass('video-playing');
	}

	function pauseVideo() {
		player.pause();
		wasPlaying = true;
		isPlaying = false;
	}

	function playVideo() {
		isAutoPlay = false;
		player.setCurrentTime(0);
		$heroVideo.css('opacity', '0');
		$heroImage.fadeOut(750, function () {
			isPlaying = true;
			player.play();
			$heroVideo.css('opacity', '0');
			$('.HeroVideo').addClass('video-playing');
		});
	}

	function togglePlay() {
		player.getPaused().then(function (paused) {
			if (paused) {
				isPlaying = true;
				player.play();
			} else {
				isPlaying = false;
				player.pause();
			}
		}).catch(function (error) {
			// an error occurred
		});
	}

	function windowResized() {
		headerH = header.height();
		winH = win.height();

		var h = winH - headerH;
		var hvh = $heroVideo.height();

		if (hvh > h) {
			var t = -Math.floor((hvh - h) / 2);
			$heroVideo.css('transform', 'translateY(' + t + 'px)');
		} else {
			$heroVideo.css('transform', 'translateY(0px)');
		}
	}

	function windowScrolled() {}

	init();
});