'use strict';

jQuery(function ($) {

  var videoRatio = 9.0 / 16.0;
  var playInterval;
  var isPlaying;
  var isAnimating = false;
  var lastSection;
  var currentSection;
  var sectionPositions = {};
  var winH = window.innerHeight;
  var winW = window.innerWidth;
  var win = $(window);
  var scrollY,
      lastScrollY = 0;
  var scrollDirection = 0;
  var oldIndex = 1;
  var newIndex = 1;
  var body = $('body');
  var lastWidth;
  var currentWidth;

  function init() {
    $(window).on('scroll', windowScrolled);
    $(window).on('resize', resizeAdjustments);

    currentWidth = lastWidth = $(window).width();

    // Set the viewport height
    var vh = window.innerHeight;
    document.documentElement.style.setProperty('--vh', vh + 'px');

    initClicks();
    //initParallax();
    checkFade(true);
  }

  function initParallax() {
    var scrollTop;
    var imgPos1;
    var imgPos2;
    var imgPos3;
    var b1 = $('.bulb1');
    var b2 = $('.bulb2');
    var b3 = $('.bulb3');
    if (b1) {
      $(window).scroll(function () {
        scrollTop = $(window).scrollTop();
        imgPos1 = Math.round(scrollTop / 4) + 'px';
        b1.css('transform', 'translateY(' + imgPos1 + ')');

        imgPos2 = Math.round(scrollTop / 7) + 'px';
        b2.css('transform', 'translateY(' + imgPos2 + ')');

        imgPos3 = Math.round(scrollTop / 2) + 'px';
        b3.css('transform', 'translateY(' + imgPos3 + ')');
      });
    }
  }

  function initClicks() {
    $('.taphover, .button').on('touchstart', function (e) {
      'use strict'; //satisfy code inspectors

      var link = $(this); //preselect the link
      if (link.hasClass('hover')) {
        return true;
      } else {
        link.addClass('hover');
        $('a.taphover').not(this).removeClass('hover');
        $('a.button').not(this).removeClass('hover');
        //e.preventDefault();
        return true; //extra, and to make sure the function has consistent return points
      }
    });

    $('.taphover, .button').on('touchend', function (e) {
      'use strict'; //satisfy code inspectors

      var link = $(this); //preselect the link
      if (link.hasClass('hover')) {
        link.removeClass('hover');
        return true;
      }
    });

    $('.panel-click').click(function (e) {
      e.preventDefault();
      $(window).trigger('dismissPanels');
      return false;
    });
  }

  function resizeAdjustments() {
    var headerH = $('#SiteHeader').height();
    var viewportRatio = (document.body.clientHeight - headerH) / document.body.clientWidth;
    lastWidth = currentWidth;
    currentWidth = $(window).width();

    // Set the viewport height
    var vh = window.innerHeight;
    document.documentElement.style.setProperty('--vh', vh + 'px');

    if (videoRatio > viewportRatio) {
      if (!$('body').hasClass('video-stretch')) {
        $('body').addClass('video-stretch');
      }
    } else {
      if ($('body').hasClass('video-stretch')) {
        $('body').removeClass('video-stretch');
      }
    }

    checkFade();
    setSectionTops();
    determineSection();
  }

  function getYOffset() {
    // get distance scrolled from the top
    var pageY;
    if (typeof window.pageYOffset == 'number') {
      pageY = window.pageYOffset;
    } else {
      pageY = document.documentElement.scrollTop; // IE
    }
    return pageY;
  }

  function pad(number, length) {
    // pad numbers with leading zeros for JPEG sequence file names
    var str = '' + number;while (str.length < length) {
      str = '0' + str;
    }return str;
  }

  function windowScrolled() {
    checkFade();
    determineSection();
  }

  function determineSection() {
    // grab locations of sections only on first scroll
    if ($.isEmptyObject(sectionPositions)) setSectionTops();

    lastScrollY = scrollY;
    scrollY = win.scrollTop();

    var aboutH = $('.HeroAbout').height();
    var aboutW = $('.HeroAbout').width();

    //$('.HeroAbout .inner').css('transform', 'translate3d('+((scrollY*.15)-((aboutH/aboutW)*$(window).width()/7))+'px,0,0)');

    $.each(sectionPositions, function (index, value) {
      if (scrollY > value) currentSection = index;
    });

    if (!isAnimating && lastSection != currentSection) {
      highlightSection($('.' + currentSection));
      lastSection = currentSection;
    }
  }

  function highlightSection(navItem) {
    $('body').removeClass(lastSection);
    $('body').addClass(currentSection);
  }

  function setSectionTops() {
    $('section').each(function (index) {
      var sectionName = $(this).attr('id');
      //sectionPositions[sectionName] = $(this).offset().top - (winH/2);
      sectionPositions[sectionName] = $(this).offset().top - winH / 1.1;
    });
  }

  function clearPlayInterval() {
    clearInterval(playInterval);
    playInterval = null;
  }

  function checkFade(isFirstCheck) {
    var winScrollTop = window.scrollY;
    winH = window.innerHeight;
    winW = window.innerWidth;
    var hOffset = winW * .055;

    var winScrollTopWithHeight = winScrollTop + winH * .5;
    var winScrollTopWithFullHeight = winScrollTop + winH;
    var top = window.scrollY;
    var a;
    var b;
    var delay = 100;
    var maxDur = 2000;
    var h;
    var t;
    var o;
    var top;
    var bot;

    if (winScrollTop > 100) {
      if (!body.hasClass('scrolled')) {
        body.addClass('scrolled');
      }
    } else {
      if (body.hasClass('scrolled')) {
        body.removeClass('scrolled');
      }
    }
    /*$('.fader:visible').not('.faded').each(function() {
      a = $(this).offset().top;
      b = winScrollTopWithFullHeight;
      
      if (a < b && !$('this').hasClass('faded')) {
        delay += 200;
        delay = delay > maxDur ? maxDur : delay;
        $(this).addClass('faded');
        $(this).css("transition-delay", delay + "ms");
      }
    });*/

    // toggle active / inactive
    /*$('.activate-scroll').each(function(){  
      var t = $(this);
      o = t.offset()
      top = o.top + hOffset;
      bot = top + (t.outerHeight()) - (hOffset * 2);
      b = winScrollTopWithHeight;     
      
      if (top < b && bot > b) {
        if(!t.hasClass('active')){
          if(isFirstCheck && t.hasClass('hero-image-with-text')){
            setTimeout(function(){
              t.addClass('active');
            }, 800);  
          } else {
            t.addClass('active');
          }
        }
      } else if(top >= b || bot <= b){
        if(t.hasClass('active')){
          t.removeClass('active');
        }
      }
    });*/
  }

  init();
});