'use strict';

jQuery(function ($) {

				var isRecoverFormShown = false;
				var isCreateAccountFormShown = false;

				function init() {
								initClicks();
				}

				function initClicks() {
								$('.AccountOverlay .close').click().click(function (e) {
												e.preventDefault();
												$(window).trigger('dismissPanels');
												return false;
								});

								$('.AccountOverlay #RecoverPasswordOverlayBtn').click(function (e) {
												e.preventDefault();
												toggleRecoverPasswordForm();
												return false;
								});

								$('.AccountOverlay #HideRecoverPasswordOverlayBtn').click(function (e) {
												e.preventDefault();
												toggleRecoverPasswordForm();
												return false;
								});

								$('.AccountOverlay #CreateAccountOverlayBtn').click(function (e) {
												e.preventDefault();
												toggleCreateAccountForm();
												return false;
								});

								$('.AccountOverlay #HideCreateAccountOverlayBtn').click(function (e) {
												e.preventDefault();
												toggleCreateAccountForm();
												return false;
								});
				}

				function toggleRecoverPasswordForm() {
								isRecoverFormShown = !isRecoverFormShown;
								if (isRecoverFormShown) {
												$('#CustomerLoginFormOverlay').stop().fadeOut(function () {
																$('#RecoverPasswordFormOverlay').removeClass('hide').stop().hide().fadeIn();
												});
								} else {
												$('#RecoverPasswordFormOverlay').stop().fadeOut(function () {
																$('#CustomerLoginFormOverlay').stop().hide().fadeIn();
												});
								}
				}

				function toggleCreateAccountForm() {
								isCreateAccountFormShown = !isCreateAccountFormShown;
								if (isCreateAccountFormShown) {
												$('#CustomerLoginFormOverlay').stop().fadeOut(function () {
																$('#CreateAccountFormOverlay').removeClass('hide').stop().hide().fadeIn();
												});
								} else {
												$('#CreateAccountFormOverlay').stop().fadeOut(function () {
																$('#CustomerLoginFormOverlay').stop().hide().fadeIn();
												});
								}
				}

				init();
});