'use strict';

jQuery(function ($) {

	function init() {
		initClicks();
		initURLS();
	}

	function initClicks() {

		$('.ProductGridItem .add-to-cart').click(function (e) {
			e.preventDefault();
			var productID = $(this).attr('data-product-id');
			var variantID = $(this).attr('data-variant-id');
			var quantity = 1;
			if (productID && variantID) {
				$(window).trigger('addSingleItemToCart', [productID, variantID, quantity]);
			}
			return false;
		});

		$('.ProductGridItem .option-btn').click(function (e) {
			e.preventDefault();
			var variantID = $(this).attr('data-variant-id');
			var parent = $(this).closest('.ProductGridItem');
			parent.addClass('active');
			parent.find('.option-btn').removeClass('active');
			$(this).addClass('active');
			parent.find('.add-to-cart').attr('data-variant-id', variantID);
			updateGridItemURLs(parent, variantID);
			return false;
		});
	}

	function initURLS() {
		$('.ProductGridItem.multi-variant').each(function () {
			var item = $(this);
			var variantID = $(this).find('.option').first().find('a').attr('data-variant-id');
			if (variantID) {
				updateGridItemURLs(item, variantID);
			}
		});
	}

	function updateGridItemURLs(item, variantID) {
		var baseURL = item.attr('data-url');
		var url = baseURL + '?variant=' + variantID;
		item.find('a').attr('href', url);
	}

	init();
});