'use strict';

jQuery(function ($) {

  var lastWidth;
  var currentWidth;
  var winH;

  function init() {
    currentWidth = lastWidth = $(window).width();

    if ($('.modalwrap.newsletter').length > 0) {
      var cnt = getCount();

      if (cnt <= 1) {
        setTimeout(showNewsletterOverlay, 5000);
      }

      $('.modalwrap.newsletter .close').click(function (event) {
        event.stopPropagation();
        hideNewsletterOverlay();
        return false;
      });

      $('.modalwrap').click(function (event) {
        if ($(event.target).hasClass('modalwrap')) {
          hideNewsletterOverlay();
        }
      });
    }
  }

  function showNewsletterThankYou() {
    $('.modalwrap .thankyou').height($('.modalwrap .form-holder').height());
    $('.modalwrap .form-holder').fadeOut(function () {
      $('.modalwrap .thankyou').hide().fadeIn();
    });
  }

  function getCount() {
    return Number($.cookie('offer_modal_shown')) ? Number($.cookie('offer_modal_shown')) : 1;
  }

  function showNewsletterOverlay() {
    var cnt = getCount();
    $.cookie('offer_modal_shown', cnt + 1, { expires: 90, path: '/' });
    $('body').css('overflow', 'hidden');
    $('.modalwrap.newsletter').addClass('active');
    $('.modalwrap.newsletter').show();
    resizeUI();
    $('.modalwrap.newsletter').hide();
    $('.modalwrap.newsletter').fadeIn({ duration: 500, start: resizeUI, step: resizeUI, complete: resizeUI });
    ga('send', 'event', 'Popup', 'View', '20% Off Intro Offer');
  }

  function hideNewsletterOverlay() {
    $('body').css('overflow', 'scroll');
    $('.modalwrap.newsletter').removeClass('active');
    $('.modalwrap').fadeOut();
    $(window).trigger('resize');
  }

  function resizeUI() {
    var headerH = $('#SiteHeader').height();
    var viewportRatio = (document.body.clientHeight - headerH) / document.body.clientWidth;
    lastWidth = currentWidth;
    currentWidth = $(window).width();
    winH = window.innerHeight;
    checkModals();
  }

  function checkModals() {
    // Modal
    $('.modalwrap .modal').each(function () {
      var t = winH - $(this).outerHeight(true);
      t = t > 0 ? t / 2.0 : 0;
      $(this).css('top', t + 'px');

      $(this).height('auto');
      setHeightByChildren($(this));
      var h = $(this).height();

      if (winH > h) {
        $(this).addClass('v-center');
      } else {
        $(this).removeClass('v-center');
      }
    });
  }

  function setHeightByChildren(parent) {
    var h = 0;
    parent.children().each(function () {
      if ($(this).is(":visible")) {
        h += $(this).outerHeight(true);
      }
    });
    parent.height(h);
  }

  init();
});