'use strict';

jQuery(function ($) {

	var isAnimating;
	var native_width = 0;
	var native_height = 0;
	var magnify_offset;
	var mx = 0;
	var my = 0;
	var lastXPos = 0;
	var lastYPos = 0;
	var lastScrolledXPos = 0;
	var lastScrolledYPos = 0;
	var paginationHeight = 20;
	var currentProduct;
	var win;
	var productInfo;
	var header;

	function init() {
		var win = $(window);

		if ($('.HomePress').length > 0) {
			slideshow();
		}
	}

	function slideshow() {

		$('.HomePress .ProductPressGrid').each(function () {
			$(this).on('init', function (event, slick) {});
		});

		$('.HomePress .ProductPressGrid').slick({
			centerMode: true,
			centerPadding: '100px',
			slidesToShow: 5,
			slidesToScroll: 1,
			lazyLoad: 'ondemand',
			arrows: false,
			autoplay: true,
			autoplaySpeed: 1000,
			adaptiveHeight: false,
			draggable: false,
			infinite: true,
			swipe: isTouchDevice(),
			dots: false,
			fade: false,
			responsive: [{
				breakpoint: 1199,
				settings: {
					slidesToShow: 5,
					slidesToScroll: 5,
					infinite: true,
					dots: false,
					centerPadding: '50px'
				}
			}, {
				breakpoint: 1023,
				settings: {
					slidesToShow: 3,
					slidesToScroll: 3,
					infinite: true,
					dots: false,
					centerPadding: '100px'
				}
			}, {
				breakpoint: 767,
				settings: {
					slidesToShow: 1,
					slidesToScroll: 1,
					infinite: true,
					dots: false,
					centerPadding: '150px'
				}
			}, {
				breakpoint: 549,
				settings: {
					slidesToShow: 1,
					slidesToScroll: 1,
					infinite: true,
					dots: false,
					centerPadding: '100px'
				}
			}]
		}).on('beforeChange', function (event, slick, currentSlide, nextSlide) {});

		$('.HomePress').click(function (event) {
			event.stopPropagation();
			$('.HomePress .ProductPressGrid').slick('slickNext');
			return false;
		});
	}

	function isTouchDevice() {
		return 'ontouchstart' in document.documentElement;
	}

	init();
});